/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin, padding and set font inherit to all elements */
* {
  padding: 0;
  margin: 0;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Remove default underline from A elements */
a,
a:visited,
a:active {
  text-decoration: none;
}

/* Remove list styles on ul, ol elements */
ul,
ol {
  list-style: none;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Set core buttons and inputs defaults */
button,
[type=button],
[type=reset],
[type=submit] {
  border: 0;
  cursor: pointer;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --duration-2: 200ms;
}

body {
  background-color: var(--blue-2);
}

:root {
  --ff-montserrat: "Montserrat", sans-serif;
  --fs-1: 0.813rem;
  --fs-2: 0.938rem;
  --fs-3: 1.125rem;
  --fs-4: 2rem;
  --fs-5: 2.5rem;
  --fs-6: 4.5rem;
  --fw-7: 700;
}
@media screen and (max-width: 23.43625em) {
  :root {
    --fs-5: clamp(1.5rem, 8vw, 2.5rem);
    --fs-6: clamp(3rem, 16vw, 4.5rem);
  }
}
@media screen and (min-width: 64em) {
  :root {
    --fs-5: clamp(2.125rem, 3.3vw, 2.5rem);
    --fs-6: clamp(4rem, 6vw, 4.5rem);
  }
}

body {
  font-family: var(--ff-montserrat);
  font-size: var(--fs-2);
  font-weight: var(--fw-7);
}

h1 {
  font-size: var(--fs-4);
  color: var(--blue-6);
}

h2 {
  font-size: var(--fs-3);
}

:root {
  --linear-gradient-1: linear-gradient(100deg, hsl(236, 72%, 79%), hsl(237, 63%, 64%));
  --blue: hsl(237, 63%, 64%);
  --blue-2: hsl(240, 78%, 98%);
  --blue-3: hsl(234, 14%, 85%);
  --blue-4: hsl(234, 14%, 74%);
  --blue-6: hsl(233, 13%, 49%);
  --blue-8: hsl(232, 13%, 33%);
  --blue-hsl: 237, 63%, 64%;
  --blue-3-hsl: 234, 14%, 85%;
  --white: hsl(0, 0%, 100%);
}

.options {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 1.5rem;
  margin-block: 2.5rem 5rem;
}
.options__label {
  color: var(--blue-4);
}

.toggle__checkbox {
  width: 0;
  position: absolute;
  opacity: 0;
  appearance: none;
}
.toggle__custom {
  --toggle-width: 3.5rem;
  --toggle-height: 2rem;
  --toggle-padding: 0.25rem;
  width: var(--toggle-width);
  height: var(--toggle-height);
  display: block;
  position: relative;
  background: var(--linear-gradient-1);
  border-radius: calc(var(--toggle-height) / 2);
  padding: var(--toggle-padding);
  transition: opacity var(--duration-2) ease;
  cursor: pointer;
}
.toggle__custom:hover, .toggle__checkbox:focus-visible + .toggle__custom {
  opacity: 0.7;
}
.toggle__custom::after {
  content: "";
  --toggle-circle-size: calc(var(--toggle-height) - (var(--toggle-padding) * 2));
  display: block;
  width: var(--toggle-circle-size);
  height: var(--toggle-circle-size);
  background-color: var(--white);
  border-radius: 50%;
  transition: transform var(--duration-2) ease;
}
.toggle__checkbox:checked + .toggle__custom::after {
  transform: translateX(100%);
}

.card {
  width: min(100%, 25rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--white);
  border-radius: 0.625rem;
  padding: 2.25rem 1.75rem 1.875rem;
  color: var(--blue-6);
  box-shadow: 0 0.3125rem 1.25rem -0.25rem hsla(var(--blue-hsl), 0.2);
}
@media screen and (min-width: 64em) {
  .card {
    width: min(100%, 21.875rem);
  }
  .card:nth-child(1) {
    border-start-end-radius: 0;
    border-end-end-radius: 0;
  }
  .card:nth-child(3) {
    border-start-start-radius: 0;
    border-end-start-radius: 0;
  }
}
.card--blue {
  background-image: var(--linear-gradient-1);
  color: var(--blue-2);
}
@media screen and (min-width: 64em) {
  .card--blue {
    padding-block: 3.5rem 3.125rem;
  }
}
.card__price {
  display: inline-flex;
  align-items: center;
  column-gap: 0.375rem;
  margin-block: 1.875rem 2rem;
  font-size: var(--fs-5);
  color: var(--blue-8);
}
.card--blue .card__price {
  color: var(--white);
}
.card__price span {
  font-size: var(--fs-6);
}
.card__list {
  width: 100%;
  margin-block-end: 2rem;
}
.card__item {
  --border-color: var(--blue-3);
  padding-block: 1.125rem;
  border-block-end: 0.0625rem solid var(--border-color);
  text-align: center;
}
.card__item:first-of-type {
  border-block-start: 0.0625rem solid var(--border-color);
}
.card--blue .card__item {
  --border-color: hsla(var(--blue-3-hsl), 0.7);
}
.card__button {
  width: 100%;
  position: relative;
  background: linear-gradient(var(--white), var(--white)) padding-box, linear-gradient(100deg, hsl(236deg, 72%, 79%), hsl(237deg, 63%, 64%)) border-box;
  border: 0.0625rem solid transparent;
  border-radius: 0.375rem;
  padding: 1rem;
  font-size: var(--fs-1);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.125em;
  color: var(--white);
  overflow: hidden;
  transition: color var(--duration-2) ease;
}
.card__button span {
  position: relative;
  z-index: 1;
}
.card__button::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  background-image: var(--linear-gradient-1);
  transition: opacity var(--duration-2) ease;
}
.card__button:is(:hover, :focus-visible) {
  color: var(--blue);
}
.card__button:is(:hover, :focus-visible)::after {
  opacity: 0;
}
.card__button--white {
  background: transparent;
  border-color: var(--white);
  color: var(--blue);
}
.card__button--white::after {
  background: var(--white);
}
.card__button--white:is(:hover, :focus-visible) {
  color: var(--white);
}

.main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: url("../images/bg-top.svg") top -35px right -195px no-repeat, url("../images/bg-bottom.svg") bottom left -180px no-repeat;
  padding: 4.375rem 1.5rem;
}
@media screen and (min-width: 48em) {
  .main {
    background-position: top right -80px, bottom left;
  }
}
@media screen and (min-width: 64em) {
  .main {
    background-position: top right, bottom left;
  }
}
.main__cards {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 2rem;
}
@media screen and (min-width: 64em) {
  .main__cards {
    flex-direction: row;
  }
}